* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    font-family: sans-serif;
}

.background__image {
    background-color: #000;
    height: 100vh;
    background-image: url('https://images.unsplash.com/photo-1587313170527-446f86d0c3d9?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=880&q=80');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.background__image nav {
    padding-top: 2.5%;
    color: #000;
    display: flex;
    justify-content: space-around;
    align-items: baseline;
    margin: 0 auto;
    font-size: 1.25rem;
}

.background__image .cart {
    flex-basis: 33.333%;
    text-align: right;
    padding-right: 5%;
}

.cart p {
    font-size: 1.25rem;
    font-weight: 600;
    cursor: pointer;
    display: inline-block;
}

.background__image nav ul {
    display: flex;
    justify-content: space-evenly;
    flex-basis: 33.333%;
}

.background__image nav ul li {
    margin: 0;
    word-break: keep-all;
    cursor: pointer;
    font-weight: 600;
}

.logo {
    font-size: 2.25rem;
    flex-basis: 33.333%;
    text-align: center;
}

.main__content {
    position: absolute;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    left: 0;
    right: 0;
    top: 50%;
    bottom: 0;
    margin: auto;
}

.main__content p {
    font-weight: bold;
    font-size: 3.5rem;
    margin-bottom: 1.75rem;
    text-shadow: 2px 2px 4px #000;
    word-spacing: 6px;
    letter-spacing: 1px;
}

.main__content button {
    background-color: #fff;
    color: transparent;
    color: black;
    font-weight: 600;
    width: 200px;
    border: none;
    outline: 0;
    height: 4rem;
    font-size: 1.15rem;
    cursor: pointer;
    transition: .3s ease;
}

.main__content button:hover {
    color: #fff;
    background-color: #000;
}

.info {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.info h2 {
    font-size: 2rem;
    letter-spacing: 0.15px;
    word-spacing: 1px;
    margin: 3.5% 0 2.5%;
}

.info p {
    margin-bottom: 7%;
}

.product__images {
    display: flex;
    flex-direction: row;
    width: 87.5%;
    margin: 0 auto;
    margin-bottom: 7%;
}

.product__images div {
    width: 33.3333%;
    height: 550px;
    background-position: center;
    background-size: cover;
}

.product__image_one {
    background-image: url('..//public/images/image2.png');
}

.product__image_two {
    background-image: url('..//public/images/image3.png');
}

.product__image_three {
    background-image: url('..//public/images/image1.png');
}

.images__button {
    background-color: rgb(255, 82, 82);
    color: white;
    font-weight: 600;
    width: 175px;
    border: none;
    outline: 0;
    height: 4rem;
    font-size: 1.1rem;
    cursor: pointer;
    display: block;
    margin: 0 auto 2.5%;
}

.fullWidthPicture {
    margin-top: 5%;
    background-position: center;
    background-size: contain;
    background-image: url('..//public/images/fullWidthPic.jpg');
    width: 100%;
    height: 100vh;
}

.suggestions p {
    text-align: center;
    margin: 2.5% 0;
}

.suggestions p:first-of-type {
    font-size: 2.25rem;
    font-weight: 600;
    margin-top: 5%;
}

.suggestions p:nth-of-type(2) {
    font-size: 1.1rem;
}

.suggestions button {
    background-color: rgb(255, 82, 82);
    color: white;
    font-weight: 600;
    width: 175px;
    border: none;
    outline: 0;
    height: 4rem;
    font-size: 1.1rem;
    cursor: pointer;
    display: block;
    margin: 0 auto 2.5%;
}

.suggested__items {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 80%;
    margin: 10% auto;
}

.suggested__items .single__item {
    height: 40vh;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 27.5%;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    cursor: pointer;
}

.single__item:first-of-type {
    background-image: url('..//public/images/item1.png');
}

.single__item:nth-of-type(2) {
    background-image: url('..//public/images/item2.png');
}

.single__item:nth-of-type(3) {
    background-image: url('..//public/images/item3.png');
}

footer {
    color: #fff;
    background-color: #a5564a;
    height: 27.5vh;
}

footer p {
    margin-left: 12.5%;
    font-size: 1.9rem;
    font-weight: 500;
    padding-top: 2.5rem;
}

footer ul {
    margin-left: 12.5%;
    margin-top: 1.3rem;
}

footer ul li {
    margin-top: 0.75rem;
    font-size: 0.85rem;
    font-weight: 400;
    word-spacing: 1px;
}